.App
{
  /* Background color of the entire web app */
  background-color: #06BC9B;

  /* Font for the website */
  font-family: "Trebuchet MS";

  overflow-x: hidden;
}

button
{
  font-family: "Trebuchet MS";
}

/* Intro paragraph */
p
{
    background-color: #fff;
    color: #333;

    font-size: 25px;
    text-align: left;

    padding-top: 20px;
    padding-bottom: 20px;

    padding-left: 30px;
    padding-right: 30px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}



